import React, { useEffect, useState } from 'react'
import './OrderDetail.css'
import { useLocation,useHistory } from 'react-router-dom'
import NavbarBuyer from '../../layout/Header/NavbarBuyer'
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import Divider from '@mui/material/Divider';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import Avatar from '@mui/material/Avatar';
import axios from 'axios'
import CircularProgress from '@mui/material/CircularProgress';
import ButtonM from '@mui/material/Button';
import format from 'date-fns/format'
import { useDispatch, useSelector } from 'react-redux'
import { useAlert } from "react-alert";


const OrderDetail = () => {
    const { search } = useLocation();
    const history = useHistory();
    const alert = useAlert();

    const [allData, setAllData] = useState({})
    const [allMessages, setAllMessages] = useState([])
    const [modalLoader, setModalLoader] = useState(false);
    const { user } = useSelector(state => state.user)
    const [text, setText] = useState('')

    useEffect(() => {
        const order = new URLSearchParams(search).get('order');
        const userTo = new URLSearchParams(search).get('userTo');
        const gigId = new URLSearchParams(search).get('gigId')
        if (order && userTo && gigId) {
            setAllData({})
            setModalLoader(true)
            axios.get(`/api/v1/getBuyerOrders?order=${order}&&userTo=${userTo}&&gigId=${gigId}`)
                .then(res => {
                    console.log(res);
                    setModalLoader(false)
                    setAllData(res.data.singleOrder)
                    setAllMessages(res.data.singleOrder.order.chatBetween)
                })
                .catch(err => {
                    console.log(err);
                    setModalLoader(false)

                })
        }


        const interval = setInterval(() => {
            axios.get(`/api/v1/getOrdersChat?orderId=${order}`)
                .then(res => {
                    console.log(res);
                    setAllMessages(res.data.chat)
                })
                .catch(err => {
                    console.log(err);
                    // setModalLoader(false)

                })
        }, 2000)

        return () => clearInterval(interval)
    }, [search])


    const sendChatData = () => {

        if (text.trim().length > 0) {
            axios.post(`/api/v1/postOrdersChat?orderId=${allData?.order._id}&&userId=${user._id}&&text=${text}&&image=${user?.avatar?.url || null}&&name=${user?.name}`)
                .then(res => {
                    console.log(res);

                })
                .catch(err => {
                    console.log(err);

                })
        }
        setText('')
    }


    const handleAcceptOrder = () =>{
        let obj = {
            sellerId:allData?.userTo?._id,
            orderId:allData?.order?._id,
            projects:{
                desc:allData?.order?.description,
                gigId:allData?.gigId?._id
            }
        }

        axios.post('/api/v1/orderAcceptByClient',obj)
        .then(res=>{
            console.log(res)
            alert.success("Order Accepted Successfully")

            history.push('/')
        })
        .catch(err=>{
            console.log(err)
            alert.error("error in accepting the order")
        })

    }


    return (
        <>
            <NavbarBuyer />



            <div className='orderDetailContainer'>

                {modalLoader &&
                    <div style={{ width: '100%', height: '100%', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                        <CircularProgress sx={{ color: 'teal', }} />
                    </div>
                }

                {modalLoader === false && Object.keys(allData).length > 0 &&
                    <>
                        <div className='left'>
                            <Card sx={{ minWidth: 275, borderLeft: '4px solid teal', boxShadow: '0 0 10px rgba(0,0,0,0.2)' }}>
                                <CardContent>
                                    <Typography sx={{ fontSize: 24 }} gutterBottom>
                                        Order is now in the works
                                    </Typography>

                                    <Typography variant="body2">
                                        We are here for your help.
                                        <br />
                                        You should receive your delivery <span style={{ fontWeight: 'bold' }}>{allData.order.endAt.substring(0, 10)}</span>

                                    </Typography>
                                </CardContent>

                            </Card>



                            <Card sx={{ minWidth: 275, borderLeft: '4px solid teal', boxShadow: '0 0 10px rgba(0,0,0,0.2)', marginTop: '30px' }}>
                                <CardContent>
                                    <Typography sx={{ fontSize: 24 }} gutterBottom>
                                        Job Description
                                    </Typography>

                                    <Typography variant="body2">

                                        {allData.order.description}

                                    </Typography>


                                    {

                                        allMessages.length > 0 &&
                                        allMessages.map((item, index) => (
                                            <ListItem key = {index} alignItems="flex-start" style={{ border: '1px solid teal', marginTop: '6px', borderRadius: '6px' }}>
                                                <ListItemAvatar>
                                                    <Avatar alt="Remy Sharp" src={item?.image || '/static/images/avatar/1.jpg'} />
                                                </ListItemAvatar>
                                                <ListItemText
                                                    primary={
                                                        <Typography>
                                                            {item.userId == user._id ? "Me":<span style={{color:'teal'}}>{item?.name}</span>}
                                                        </Typography>
                                                    }
                                                    secondary={
                                                        <React.Fragment>
                                                            <Typography
                                                                sx={{ display: 'inline' }}
                                                                component="span"
                                                                variant="body2"
                                                                color="text.primary"
                                                            >
                                                                {item.text}
                                                            </Typography>
                                                            
                                                        </React.Fragment>
                                                    }
                                                />
                                            </ListItem>
                                        ))
                                    }




                                    <div className='' style={{ width: '100%', marginTop: '6px' }}>
                                        <div className='premiumTextAreaTop' style={{ background: 'lightgrey', color: 'black', width: '100%' }}>Write Your Query here...</div>

                                        <textarea type="text" name="text" rows="10" className='premiumTextArea' placeholder="Write Your complete project's description here..."
                                            value={text} onChange={(e) => setText(e.target.value)} style={{ height: '180px', border: '1px solid lightgrey', width: '100%' }}
                                        />
                                        <div className='buttonToSearch' style={{ width: '100%' }}>
                                            <span onClick={() => { sendChatData() }}>Send</span>
                                        </div>
                                    </div>

                                </CardContent>

                            </Card>
                        </div>

                        <div className='right'>
                            <Card sx={{ padding: '10px', minWidth: 275, borderRight: '4px solid teal', boxShadow: '0 0 10px rgba(0,0,0,0.2)', height: '260px' }}>
                                <CardContent sx={{ padding: '10px', margin: 0 }}>
                                    <ListItem alignItems="flex-start" sx={{ display: 'flex', justifyContent: 'space-between', padding: 0, margin: 0 }}>
                                        <ListItemAvatar>
                                            <Avatar alt="Remy Sharp" src={allData?.order?.gigId?.image || '/static/images/avatar/1.jpg'}
                                                style={{ borderRadius: '2px', width: '6rem', height: '3rem', marginRight: '16px', }}
                                            />
                                        </ListItemAvatar>
                                        <ListItemText sx={{ width: '50%' }}
                                            // primary={item.price.name}
                                            primary={allData?.order?.price?.name}

                                        />
                                    </ListItem>


                                </CardContent>

                                <Typography sx={{ display: 'flex', justifyContent: 'space-between', padding: '20px 10px 4px 10px' }}>
                                    <div style={{ color: 'grey', fontFamily: 'sans-serif' }}>Order Place</div>
                                    <div style={{ color: 'black', fontWeight: 600, fontFamily: 'sans-serif' }}>{allData.order?.createdAt?.substring(0, 10)}</div>
                                </Typography>
                                <Typography sx={{ display: 'flex', justifyContent: 'space-between', padding: '4px 10px' }}>
                                    <div style={{ color: 'grey', fontFamily: 'sans-serif' }}>Delivery Date</div>
                                    <div style={{ color: 'black', fontWeight: 600, fontFamily: 'sans-serif' }}>{allData.order?.endAt?.substring(0, 10)}</div>
                                </Typography>
                                <Typography sx={{ display: 'flex', justifyContent: 'space-between', padding: '4px 10px' }}>
                                    <div style={{ color: 'grey', fontFamily: 'sans-serif' }}>Total Price</div>
                                    <div style={{ color: 'black', fontWeight: 600, fontFamily: 'sans-serif' }}>US${allData.order?.price?.amount}</div>
                                </Typography>
                                {   
                                    allData?.order?.status === "COMPLETE" ?
                                    <ButtonM variant="outlined" sx={{ width: '100%', marginTop: '16px' }}>COMPLETE</ButtonM>:
                                    allData.order?.userFrom === user._id ?
                                        <ButtonM variant="outlined" sx={{ width: '100%', marginTop: '16px' }} 
                                        onClick={handleAcceptOrder}
                                        >Accept Order</ButtonM> :
                                        <ButtonM variant="outlined" sx={{ width: '100%', marginTop: '16px' }}>Place Order</ButtonM>

                                }


                            </Card>
                        </div>
                    </>
                }



            </div>

        </>
    )
}

export default OrderDetail