import { CardElement, useElements, useStripe } from "@stripe/react-stripe-js";
import React, { useState,useEffect } from "react";
import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import { CardActionArea } from '@mui/material';
import axios from 'axios'
import CircularProgress from '@mui/material/CircularProgress';
import { useAlert } from "react-alert";



function CheckoutForm({ id, price, title, url, user }) {
    // collect data from the user
    const [progressBar, setProgressBar] = useState(true)
    const [empty, setEmpty] = useState(true)
    const alert = useAlert();

    // stripe items
    const stripe = useStripe();
    const elements = useElements();


   
    

    // main function
    const createSubscription = async () => {
        try {
            setProgressBar(true)
            console.log(elements.getElement(CardElement))
            // create a payment method
            // const payload = await stripe.confirmCardPayment(clientSecret, {
            //     payment_method: {
            //       card: elements.getElement(CardElement)
            //     }
            //   });
            const cardElementContainer = document.querySelector('#card-element');

            let cardElementEmpty = cardElementContainer.classList.contains('StripeElement--complete');
              if (!cardElementEmpty) {
                alert.error(`Incorrect Card Info`);
                setProgressBar(false);
                return
              }


            const paymentMethod = await stripe?.createPaymentMethod({
                type: "card",
                card: elements?.getElement(CardElement),
                billing_details: {
                    name: user.name,
                    email: user.email,
                },
            });

            // call the backend to create subscription
             await axios.post(`/api/v1/create-subscription-video/${id}`,
                {
                    paymentMethod: paymentMethod?.paymentMethod?.id,


                }).then((res) => {

                    console.log(res)
                    if(res.data.url){
                        window.location.href = res.data.url;

                    }
                }
                ).catch(err=>{
                    alert.error("Network Error")
                });

            

            
            setProgressBar(false)
        } catch (error) {
            console.log(error);
            setProgressBar(false)
        }
    };

    const cardStyle = {
        style: {
            base: {
                color: "#32325d",
                fontFamily: 'Arial, sans-serif',
                fontSmoothing: "antialiased",
                border: '1px solid black',
                fontSize: "16px",
                padding: '10px',
                "::placeholder": {
                    color: "#32325d"
                }
            },
            invalid: {
                fontFamily: 'Arial, sans-serif',
                color: "#fa755a",
                iconColor: "#fa755a",

            },

        }
    };

    return (
        <Grid container xs={12}>

            <Grid container xs={12}>
                <Grid item xs={4} md={2}>
                    <img src={url} style={{ height: '80px', width: '80px', boxShadow: '2px 2px 4px black' }} />
                </Grid>
                <Grid item xs={8} md={10}>
                    <Typography variant="subtitle2" gutterBottom>
                        <span style={{ fontWeight: 'bold', }}>Title:</span>  {title}
                    </Typography>
                    <Typography variant="subtitle2" gutterBottom>
                        <span style={{ fontWeight: 'bold', }}>Price:</span> ${price}
                    </Typography>
                </Grid>
            </Grid>

            <Typography varient='h4' sx={{ mt: 3, mb: 1 }}>
                Credit or debit card
            </Typography>
            <Grid item xs={12}>
                <CardElement id='card-element' options={cardStyle} onReady={()=>setProgressBar(false)}/>  
            </Grid>
            {
                progressBar === true &&
                <div style={{ width: '100%', textAlign: 'center' }}>
                    <CircularProgress />
                </div>
            }
            <Grid item xs={12} textAlign='center'>

                <Button disabled={progressBar} 
                    variant="contained" sx={{mt:3}} style={{  width: '160px', textAlign: 'center', }} onClick={createSubscription}  >
                    Subscribe
                </Button>
            </Grid>
        </Grid>
    );
}

export default CheckoutForm;