import React, { useState } from 'react';
import { Button } from './Button';
import { Link } from 'react-router-dom';
import './NavbarSeller.css';
import Dropdown from './Dropdown';
import { MenuItems } from './MenuItems';
import { AvatarMenuItems } from './AvatarMenuItems';
import Avatar from '@mui/material/Avatar';
import { useSelector, useDispatch} from "react-redux";
import {changeNavbar} from '../../../redux/actions/userAction'
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import Divider from '@mui/material/Divider';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import axios from 'axios'


function Navbar() {
  const { loading, isAuthenticated, user } = useSelector((state) => state.user);
  const dispatch = useDispatch(); 
  const [open, setOpen] = useState(false);
  const [modalLoader, setModalLoader] = useState(false);
  const [buyerOrders, setBuyerOrders] = useState([]);

  const [click, setClick] = useState(false);
  const [dropdown, setDropdown] = useState({
    avatar:false,
    services:false
  });
  const handleNavbar = ()=>{
    dispatch(changeNavbar('buyer'))
  }

  const getOrderData = () => {

    setModalLoader(true)
    setBuyerOrders([])
    axios.get('/api/v1/getBuyerOrders?seller=true')
      .then(res => {
        console.log(res);
        setBuyerOrders(res.data.orders)
        setModalLoader(false)
      })
      .catch(err => {
        console.log(err);
        setModalLoader(false)

      })

  }


  const handleOpen = () => {
    setOpen(true)
    getOrderData()
  };
  const handleClose = () => setOpen(false);



  const handleClick = () => setClick(!click);
  const closeMobileMenu = () => setClick(false);

  const onMouseEnter = (name) => {
    if (window.innerWidth < 1020) {
      setDropdown((pre)=>({...pre,[name]:false}));
    } else {
      setDropdown((pre)=>({...pre,[name]:true}));
    }
  };

  const onMouseLeave = (name) => {
    if (window.innerWidth < 1020) {
      setDropdown((pre)=>({...pre,[name]:false}));
    } else {
      setDropdown((pre)=>({...pre,[name]:false}));
    }
  };


  const style = {
    position: 'absolute',
    top: '10%',
    left: '54%',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid lightgrey',
    borderRadius: '10px',
    boxShadow: 24,
    height: 500,
    outline: 'none',
    // p: 4,
    cursor: 'pointer',
    overflow: 'auto',
    position: 'relative',
  };


  return (
    <>
      <nav className='navbar navbar-seller'>
        <div className="navbar-inner ">
          <Link to='/dashboard' className='navbar-logo navbar-seller-text' onClick={closeMobileMenu}>
            WorkPay
            <i className="fab fa-google-wallet navbar-seller-text" aria-hidden="true"></i>


          </Link>
          <div className='menu-icon' onClick={handleClick}>
            <i className={click ? 'fas fa-times navbar-seller-text' : 'fas fa-bars navbar-seller-text'} />
          </div>
          <ul className={click ? 'nav-menu active navbar-seller' : 'nav-menu'}>
            <li className='nav-item'>
              <Link to='/dashboard' className='nav-links navbar-seller-text' onClick={closeMobileMenu}>
                Dashboard
              </Link>
            </li>
            <li
              className='nav-item'
              onMouseEnter={()=>onMouseEnter('services')}
              onMouseLeave={()=>onMouseLeave('services')}
            >
              <Link
                to='/profile'
                className='nav-links navbar-seller-text'
                onClick={closeMobileMenu}
              >
                Profile 
                {/* <i className='fas fa-caret-down' /> */}
              </Link>
              {/* {dropdown.services && <Dropdown MenuItems={MenuItems} />} */}
            </li>
            <li className='nav-item'>
              <Link
                to='/videoCourse'
                className='nav-links navbar-seller-text'
                onClick={closeMobileMenu}
              >
                Videos
              </Link>
            </li>
            <li className='nav-item'>
              <div
                // to='/orders'
                className='nav-links navbar-seller-text'
                onClick={handleOpen}
              >
                Orders
              </div>
            </li>
            <li className='nav-item'>
              <Link
                to='/settings'
                className='nav-links navbar-seller-text'
                onClick={closeMobileMenu}
              >
                Settings
              </Link>
            </li>
            <li>
              <Link
                to='/login'
                className='nav-links-mobile '
                onClick={closeMobileMenu}
              >
                JOIN Us
              </Link>
            </li>
          </ul>
          <div>
            
            {isAuthenticated 
            ?
            <>
              <div className="avatar-mobile-display">
              
              <Link to='/'
              className="switch-dashboard" onClick={handleNavbar}
              >Switch to Buying</Link>
              <li
              className='nav-item'
              onMouseEnter={()=>onMouseEnter('avatar')}
              onMouseLeave={()=>onMouseLeave('avatar')}
            >
              <Avatar  
              alt="Remy Sharp" src={user.avatar?.url ? user.avatar?.url:"./Profile.png" } />
              {dropdown.avatar && <Dropdown MenuItems={AvatarMenuItems} right="2%" avatar={true} />}
          </li>
              </div>
            </>
            :<Button text="Join us" link="/login"/>
            
            }

          </div>
        </div>
      </nav>


      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        {/* <Box sx={style}> */}

        <List sx={[, style, { width: '100%', maxWidth: 360, bgcolor: 'background.paper' }]}>

          {buyerOrders.length > 0 && buyerOrders.map((item, index) => (
            <Link style={{textDecoration: 'none',color:'inherit'}} onClick={()=>setOpen(false)}
            to={`/OrderDetail?order=${item._id}&&userTo=${item.userTo.id}&&gigId=${item.gigId.id}`}>
              <ListItem alignItems="flex-start">
                <ListItemAvatar>
                  <Avatar alt="Remy Sharp" src={item?.gigId?.image} />
                </ListItemAvatar>
                <ListItemText
                  primary={<>
                      <div style={{display:'flex',justifyContent:'space-between',alignItems:'center'}}>
                      <div style={{width:'70%'}}>
                        {item.price.name.substring(0,24)+"..."} 
                      </div>
                      <Typography 
                        sx={{fontSize:'10px',borderRadius:'4px', backgroundColor:'teal',color:'white',width:'66px',padding:'4px 0',textAlign:'center',}}
                      >
                        {item.createdAt.substring(0,10)}
                      </Typography>
                      </div>
                  </>}
                  secondary={
                    <React.Fragment>
                      <Typography
                        sx={{ display: 'inline' }}
                        component="span"
                        variant="body2"
                        color="text.primary"
                      >
                        {item.userTo.name}
                      </Typography>
                      
                      {item.description.substring(0,56)+"..."}

                      

                    </React.Fragment>
                  }
                />
                
              </ListItem>
              <Divider variant="inset" component="li" />
            </Link>
          ))
          }



          {modalLoader &&
            <div style={{ width: '100%', height: '100%', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
              <CircularProgress sx={{ color: 'teal', }} />
            </div>
          }
          {
            (modalLoader == false && buyerOrders.length == 0) &&
            <div style={{ width: '100%', height: '100%', display: 'flex', justifyContent: 'center', alignItems: 'center',flexDirection:'column' }}>
            <i class="fa-solid fa-shop" 
            style={{color:'teal',fontSize:'60px',marginBottom: '20px'}}
            ></i>
              <div style={{color:'teal',fontWeight:'bold',fontSize:'20px',fontFamily:'sans-serif'}}>
                No Order Found
              </div>
            </div>

          }




        </List>
        {/* </Box> */}
      </Modal>
    </>
  );
}

export default Navbar;
