import React from "react";
import { Link } from "react-router-dom";
import Avatar from '@mui/material/Avatar';
import StarIcon from '@mui/icons-material/Star';
import FavoriteIcon from '@mui/icons-material/Favorite';
const GigsCard = ({gig,user}) => {
  const options = {
    value: 4,
    readOnly: true,
    precision: 0.5,
  };
  return (

    <Link className="productCard" to={`/gig/${gig._id}`}>
      <img src={gig.images.url}
        alt="nothing" />
      <div className="product-person">
        <Avatar src={user.avatar?.url ? user.avatar.url:'./Profile.png'}  className="avatar"/>
        <div className='user-name'>{user.name}</div>
      </div>
      <p className='gig-title'>{gig.title.length>42 ? gig.title.substring(0,42) +'...' : gig.title}</p>
      <div>
        <span className="productCardSpan">
          <StarIcon style={{ color: 'orange' }} className="stars" />
          <span>{gig?.ratings}</span> ({gig?.numOfReviews})

        </span>
      </div>
      <div className="line"></div>

      <div className='price-container'>
        
        <FavoriteIcon style={{color:'grey'}}/>
        <span className="price">${gig?.price[0].amount}</span>
      </div>
    </Link>
  );
};

export default GigsCard;
