import {
    LOAD_GIG_REQUEST,
    LOAD_GIG_SUCCESS,
    LOAD_GIG_FAIL,
    CREATE_GIG_REQUEST,
    CREATE_GIG_SUCCESS,
    CREATE_GIG_FAIL,
    CREATE_GIG_RESET,
    UPDATE_GIG_REQUEST,
    UPDATE_GIG_SUCCESS,
    UPDATE_GIG_FAIL,
    UPDATE_GIG_RESET,
    GIG_DETAILS_REQUEST,
    GIG_DETAILS_SUCCESS,
    GIG_DETAILS_FAIL,
    GIG_DETAILS_RESET,
    ALL_GIGS_REQUEST,
    ALL_GIGS_SUCCESS,
    ALL_GIGS_FAIL,
    CREATE_GIGID_RESET,
    CLEAR_ERRORS
} from '../constants/gigConstants'


export const gigReducer = (state = { gigs: [] }, action) => {

    switch (action.type) {
        case LOAD_GIG_REQUEST:
        case CREATE_GIG_REQUEST:
            return {
                loading: true,
                gigs: [],
            }
        case LOAD_GIG_SUCCESS:
            return {
                loading: false,
                gigs: action.payload,
            }
        case LOAD_GIG_FAIL:
        case CREATE_GIG_FAIL:
            return {
                loading: false,
                error: action.payload
            }
        case CLEAR_ERRORS:
            return {
                ...state,
                error: null,
            };
        default:
            return state;
    }
}


export const createGigReducer = (state = {}, action) => {

    switch (action.type) {

        case CREATE_GIG_SUCCESS:
            return {
                ...state,
                loading: false,
                isCreated: action.payload,
                gigId: action.id,
            }
        // case CREATE_GIG_FAIL:
        //     return{
        //         ...state,
        //         loading:false,
        //         error:action.payload,
        //     }
        case CREATE_GIG_RESET:
            return {
                ...state,
                isCreated: false,
            }
       case CREATE_GIGID_RESET:
        return{
            ...state,
            isCreated:false
        }     
        case CLEAR_ERRORS:
            return {
                ...state,
                error: null,
            };
        default:
            return state;
    }
}


export const updateGigReducer = (state = {}, action) => {
    switch (action.type) {
        case UPDATE_GIG_REQUEST:
            return {
                ...state,
                loading: true,
            }
        case UPDATE_GIG_SUCCESS:
            return {
                ...state,
                loading: false,
                isUpdated: action.payload,
            }
        case UPDATE_GIG_FAIL:
            return {
                ...state,
                loading: false,
                error: action.payload,
            }
        case UPDATE_GIG_RESET:
            return {
                ...state,
                isUpdated: false,
            }
        case CLEAR_ERRORS:
            return {
                ...state,
                error: null,
            };

        default:
            return state;
    }
}



export const gigDetailsReducer = (state = { }, action) => {
    switch (action.type) {
      case GIG_DETAILS_REQUEST:
        case ALL_GIGS_REQUEST:
        return {
          loading: true,
          ...state,
        };
      case GIG_DETAILS_SUCCESS:
        case ALL_GIGS_SUCCESS:
        return {
          loading: false,
          detailedGig: action.payload,
        };
      case GIG_DETAILS_FAIL:
        case ALL_GIGS_FAIL:
        return {
          loading: false,
          error: action.payload,
        };
        
        case GIG_DETAILS_RESET:
            return{
                ...state,
                detailedGig:{}
            }
  
      case CLEAR_ERRORS:
        return {
          ...state,
          error: null,
        };
      default:
        return state;
    }
  };