import React, { useState, useEffect, useRef } from 'react'
import NavbarBuyer from '../../layout/Header/NavbarBuyer'
import MetaData from '../../layout/MetaData'
import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Divider from '@mui/material/Divider';
import FormControl from '@mui/material/FormControl';
import OutlinedInput from '@mui/material/OutlinedInput';
import InputLabel from '@mui/material/InputLabel';
import InputAdornment from '@mui/material/InputAdornment';
import CircularProgress from '@mui/material/CircularProgress';
import { useAlert } from "react-alert";
import axios from 'axios'

import Tags from './Tags'
import SingleVideoCard from './SingleVideoCard';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { loadStripe } from "@stripe/stripe-js";
import { Elements } from "@stripe/react-stripe-js";
import CheckoutForm from './CheckoutForm';
import { useSelector } from "react-redux";



const stripePromise = loadStripe("pk_test_51MKI38FI7iMIbxw9oFFFkFa2tQBiqgyfbzTUtXuHDmQ95Bmlcq5Kq2KyRZOJKdXlZ8UDybT9VDcTydGdIQSijq8w00kjMwNDLL");

const VideoDetailForBuyer = ({ match }) => {
    const { loading, isAuthenticated, user } = useSelector((state) => state.user);

    const alert = useAlert();
    const [courseDetail, setCourseDetail] = useState(null)
    const [open, setOpen] = useState(false);
    // For ContentUpdate
    const avatarPickImage = useRef(null);
    const [title, setTitle] = useState('');
    const [desc, setDesc] = useState('');
    const [price, setPrice] = useState('');
    const [tags, setTags] = useState([]);
    const tagRef = useRef();
    const [avatar, setAvatar] = useState('');
    const [avatarPreview, setAvatarPreview] = useState("");
    const [progressBar, setProgressBar] = useState(false)

    // For VideoUpdate
    const [videoUrl, setVideoUrl] = useState('');
    const [videoPublic_id, setVideoPublic_id] = useState('');
    const [videoTitle, setVideoTitle] = useState('');
    const [videoDesc, setVideoDesc] = useState('');
    const [openVideoModal, setOpenVideoModal] = useState(false);
    const cloudinaryRef = useRef()
    const widgetRef = useRef()
    const [videoIndex, setVideoIndex] = useState(-1)
    const [videoFileName, setVideoFileName] = useState('')
    const [openPaymentModal, setOpenPaymentModal] = useState(false)
    const [edited, setEdited] = useState(false)




    useEffect(() => {
        axios.get(`/api/v1/singleVideoItemBeforeBuying/${match.params.id}?user=false`)
            .then(res => {
                console.log(res?.data?.videoCourse)
                setCourseDetail(res?.data?.videoCourse)
            })
            .catch(err => {
                console.log(err);
            })

    }, [])
    const handleOpen = () => {
        setTitle(courseDetail?.title)
        setDesc(courseDetail?.description)
        setPrice(courseDetail?.price + "")
        setTags(courseDetail?.searchTags?.map(item => item.replaceAll(" ", "_")))
        setAvatarPreview(courseDetail?.images?.url)
        setOpen(true)
    };
    const handleClose = () => setOpen(false);

    const handleDelete = (value) => {
        const newtags = tags.filter((val) => val !== value);
        setTags(newtags);
    };
    const handleOnSubmit = (e) => {
        e.preventDefault();
        setTags([...tags, tagRef.current.value.trim().replaceAll(' ', '_')]);
        tagRef.current.value = "";
    };

    const imagePick = (e) => {
        const reader = new FileReader();
        if (e.target.files[0]) {
            setAvatar(e.target.files[0]);
        }


        reader.onload = () => {
            if (reader.readyState === 2) {
                setAvatarPreview(reader.result);
                // setAvatar(reader.result);
            }
        };

        reader.readAsDataURL(e.target.files[0]);
    };

    const submitVideoCourse = () => {
        console.log(tags.map(item => item.replaceAll("_", ' ').toLowerCase()))
        if (title.trim() === '' || title.trim().length > 80) {
            alert.error('Please enter correct title...')
            return
        }
        else if (desc.trim() === '' || desc.trim().length > 80) {
            alert.error('Please enter correct description...')
            return
        }
        else if (price.trim() === '' || isNaN(parseInt(price)) || parseInt(price) <= 0) {
            alert.error('Please enter correct amount...')
            return
        }
        else if (tags.length <= 0) {
            alert.error('Please enter some tags...')
            return
        }
        // else if (avatar === '' || avatarPreview === '') {
        //     alert.error('Please select title image...')
        //     return
        // }

        console.log("best")
        setProgressBar(true)
        const myForm = new FormData();
        avatar !== '' && myForm.append("courseImage", avatar);
        myForm.append("title", title);
        myForm.append("description", desc);
        myForm.append("price", parseInt(price));
        myForm.append("searchTags", JSON.stringify(tags.map(item => item.replaceAll("_", ' ').toLowerCase())));

        const config = { headers: { "Content-Type": avatar !== '' ? 'multipart/form-data' : 'application/json' } };

        axios.put(`/api/v1/video/${match.params.id}`, myForm, config)
            .then(response => {
                console.log(response);
                setProgressBar(false)
                handleClose()
                setCourseDetail(response.data.videoCourse)
                setTitle('')
                setDesc('')
                setPrice('')
                setTags([])
                setAvatar('')
                setAvatarPreview('')
                alert.success("Created successfully")
            })
            .catch(err => {
                setProgressBar(false)
                console.log(err)
                alert.error(err.response.data.message)
            })

    }
    const modalStyle = {
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: 600,
        bgcolor: 'background.paper',
        borderRadius: '4px',
        boxShadow: 24,
        p: 4,
        overflowY: 'auto',


    };




    // VideoFunctions
    useEffect(() => {
        cloudinaryRef.current = window.cloudinary
        widgetRef.current = cloudinaryRef.current.createUploadWidget({
            cloudName: 'freelance-learning-platform',
            uploadPreset: 'forVideoUpload',
            clientAllowedFormats: ['video', 'webp', 'mp4'],
            folder: 'freelance-learning-platform/videoCourseVideos'
        }, (error, result) => {
            if (!error && result && result.event === "success") {
                console.log(videoPublic_id)
                if (videoPublic_id !== '' && videoUrl !== '') {
                    console.log("In")
                    axios.post(`/api/v1/videoDeleteFromServer`, { public_id: videoPublic_id })
                        .then(res => {
                            console.log(res)
                            console.log("Hello 1")
                        });
                    console.log("Hello 2")

                }

                console.log('Done! Here is the image info: ', result);
                setVideoPublic_id(result.info.public_id)
                setVideoUrl(result.info.url)
                setVideoFileName(result.info.original_filename + "." + result.info.format)
            }

        }
        )
    }, [videoPublic_id, videoUrl])

    const uploadNewVideo = (index) => {
        if (videoTitle.trim() === '' || videoTitle.trim().length > 80) {
            alert.error('Please enter correct title...')
            return
        }
        else if (videoDesc.trim() === '' || videoDesc.trim().length > 80) {
            alert.error('Please enter correct description...')
            return
        }
        else if (videoPublic_id.trim() === '' || videoUrl.trim() === '' || videoFileName.trim() === '') {
            alert.error('Please Select Video...')
            return
        }

        const obj = {
            title: videoTitle.trim(),
            description: videoDesc.trim(),
            public_id: videoPublic_id.trim(),
            url: videoUrl.trim(),
            file_name: videoFileName.trim(),
        }
        setProgressBar(true)
        axios.put(`/api/v1/videoItem/${match.params.id}`, { videos: obj, index })
            .then(response => {
                setProgressBar(false)
                setVideoTitle('')
                setVideoDesc('')
                setVideoPublic_id('')
                setVideoUrl('')
                setVideoFileName('')
                setOpenVideoModal(false)
                setCourseDetail(response.data.videoCourse)
                alert.success("Success")
                console.log(response.data)
            })
            .catch(err => {
                setProgressBar(false)
                alert.error("Something went wrong")
            })


    }
    const handleCloseVideoModal = () => {
        setOpenVideoModal(false)
        if (edited) {
            setVideoTitle('')
            setVideoDesc('')
            setVideoPublic_id('')
            setVideoUrl('')
            setVideoFileName('')
            setEdited(false)
        }
    }
    const openVideoEdit = (title, desc, public_id, url, file_name, index) => {
        setVideoTitle(title)
        setVideoDesc(desc)
        setVideoPublic_id(public_id)
        setVideoUrl(url)
        setVideoFileName(file_name)
        setVideoIndex(index)
        setEdited(true)
        setOpenVideoModal(true)

    }

    const handlePaymentModal = () => {
        axios.post(`/api/v1/create-subscription-video/${match.params.id}`,{
            courseId:match.params.id
        })
        .then((res) => {

            console.log(res)
            if(res.data.url){
                window.location.href = res.data.url;

            }
        }
        ).catch(err=>{
            console.log(err)
            alert.error("Network Error")
        });
        
    }

    // const deleteSingleVideo = () => {
    //     setProgressBar(true)
    //     axios.delete(`/api/v1/videoItem/${match.params.id}?public_id=${videoPublic_id}`)
    //         .then(response => {
    //             console.log(response)
    //             setCourseDetail(response.data.videoCourse)
    //             setVideoPublic_id('')
    //             setOpenDeleteModal(false)
    //             setProgressBar(false)
    //             alert.success('Success')
    //         })
    //         .catch(err => {
    //             setProgressBar(false)
    //             alert.success('Something went wrong')
    //         })
    // }

    return (
        <>
            <MetaData title="Videos" />
            <NavbarBuyer />

            <div className="dashboard">

                <div className='dashboard-inner' >


                    <Container maxWidth="lg" >
                        <span className="dashboard-title" >Course</span>


                        <Grid container sx={{ mt: 2, mb: 1 }}>
                            <Grid container xs={12} alignItems="flex-start">
                                <Grid container alignItems="center" xs={12} sm={6} md={8} style={{}} >
                                    <Grid item xs={8} sm={8} md={8} style={{}}>
                                        <Typography gutterBottom variant="h4" component="div">
                                            {courseDetail?.title}
                                        </Typography>
                                    </Grid>
                                    <Grid item xs={4} sm={4} md={4} style={{ textAlign: 'right' }}>
                                        <Typography gutterBottom variant="h6" component="div">
                                            ${courseDetail?.price}
                                        </Typography>
                                    </Grid>
                                    <Grid item xs={12} sx={{ mb: 1 }}>
                                        <Typography color="text.secondary" variant="body1" style={{ textAlign: 'justify' }}>
                                            {courseDetail?.description}
                                            {courseDetail?.description}
                                            {courseDetail?.description}
                                        </Typography>
                                    </Grid>
                                    <Grid item xs={12} sx={{ my: 1 }}>
                                        {
                                            courseDetail?.searchTags?.map((item, index) => (
                                                <Typography key={index} color="text.secondary" variant="body2"
                                                    style={{ display: 'inline-block', textAlign: 'justify', marginRight: '4px', padding: '5px 10px', borderRadius: '3px', backgroundColor: 'grey', color: 'white' }}>
                                                    {item}
                                                </Typography>
                                            ))
                                        }

                                    </Grid>
                                    {/* <Grid item xs={12} sx={{ my: 1 }}>
                                        <Button onClick={handleOpen} variant="outlined" >EDIT COURSE</Button>
                                    </Grid> */}
                                </Grid>
                                <Grid item xs={12} sm={6} md={4} style={{ textAlign: 'right' }}>
                                    <img src={courseDetail?.images?.url}
                                        style={{ width: '160px', height: '160px', borderRadius: '6px' }} />
                                </Grid>
                            </Grid>
                            <Divider style={{ width: '100%' }} />

                            <Grid item xs={12} sx={{ my: 2 }} textAlign='right'>
                                <Button onClick={() => {
                                    handlePaymentModal() 
                                }} variant="contained" style={{ backgroundColor: 'teal', width: '160px', textAlign: 'center' }}>BUY ${courseDetail?.price}</Button>

                            </Grid>


                            <Grid spacing={2} container xs={12}>
                                {
                                    courseDetail?.videos?.length > 0 &&
                                    courseDetail?.videos?.map((item, index) => (
                                        <Grid item xs={12} sm={10} md={8} sx={{ mt: 1 }} key={index}>
                                            {/* <SingleVideoCard title={item.title}
                                                desc={item.description} url={item.url}
                                                public_id={item.public_id} index={index}
                                                id={match.params.id}
                                                openVideoEdit={() => openVideoEdit(item.title, item.description, item.public_id, item.url, item.file_name, index)}
                                                openDeleteVideo={() => handleDeleteModal(item.public_id)}

                                            /> */}

                                            <Accordion>
                                                <AccordionSummary
                                                    expandIcon={<ExpandMoreIcon />}
                                                    aria-controls="panel1a-content"
                                                    id="panel1a-header"
                                                >
                                                    <Typography>{item.title}</Typography>
                                                </AccordionSummary>
                                                <AccordionDetails>
                                                    <Typography>
                                                        {item.description}
                                                    </Typography>
                                                </AccordionDetails>
                                            </Accordion>

                                        </Grid>
                                    ))
                                }
                            </Grid>

                        </Grid>


                        {/* Detail Edit Model */}
                        <Modal
                            open={open}
                            onClose={handleClose}
                            aria-labelledby="modal-modal-title"
                            aria-describedby="modal-modal-description"
                        >
                            <Box sx={modalStyle}>
                                <TextField label="Title" variant="outlined" sx={{ width: '100%', mb: 1 }}
                                    value={title} onChange={(e) => { setTitle(e.target.value) }}
                                    inputProps={{ maxLength: 80 }}
                                />
                                <TextField label="Description" multiline variant="outlined" sx={{ width: '100%', mb: 1 }}
                                    value={desc} onChange={(e) => { setDesc(e.target.value) }}
                                    inputProps={{ maxLength: 800 }}
                                />

                                {/* Amount */}
                                <FormControl fullWidth sx={{ my: 1 }}>
                                    <InputLabel htmlFor="outlined-adornment-amount">Amount</InputLabel>
                                    <OutlinedInput
                                        id="outlined-adornment-amount"
                                        startAdornment={<InputAdornment position="start">$</InputAdornment>}
                                        label="Amount"
                                        inputProps={{ maxLength: 5 }}
                                        value={price} onChange={(e) => { setPrice(e.target.value) }}
                                    />
                                </FormControl>

                                {/* Tags */}
                                <Box sx={{ flexGrow: 1 }}>
                                    <form onSubmit={handleOnSubmit}>
                                        <TextField
                                            inputRef={tagRef}
                                            fullWidth
                                            variant="outlined"
                                            sx={{ mb: 1, mt: 1 }}
                                            margin='none'
                                            label={tags?.length < 5 ? "Tags" : ""}
                                            InputProps={{
                                                startAdornment: (
                                                    <Box sx={{ margin: "0 0.2rem 0 0", display: "flex", flexWrap: 'nowrap', }}>
                                                        {tags?.map((data, index) => {
                                                            return (
                                                                <Tags data={data} handleDelete={handleDelete} key={index} />
                                                            );
                                                        })}
                                                    </Box>
                                                ),
                                            }}
                                        />
                                    </form>
                                </Box>


                                <Button variant="outlined" onClick={() => avatarPickImage.current.click()}>
                                    ADD TITLE IMAGE
                                    <input type="file"
                                        hidden ref={avatarPickImage}
                                        onChange={imagePick}
                                        accept=".jpg, .png, .jpeg, .gif|image/*" />
                                </Button>

                                {avatarPreview != '' &&
                                    <img style={{ width: '160px', height: '160px', display: 'block', margin: '6px 0' }} src={avatarPreview} />
                                }

                                {
                                    progressBar === true &&
                                    <div style={{ width: '100%', textAlign: 'center' }}>
                                        <CircularProgress />
                                    </div>
                                }

                                <div style={{ width: '100%', textAlign: 'right' }}>
                                    <Button disabled={progressBar}
                                        variant="contained" onClick={() => { submitVideoCourse() }}>
                                        Edit
                                    </Button>
                                </div>
                            </Box>
                        </Modal>


                        {/* Video Edit Model */}
                        <Modal
                            open={openVideoModal}
                            onClose={handleCloseVideoModal}
                            aria-labelledby="modal-modal-title"
                            aria-describedby="modal-modal-description"
                        >
                            <Box sx={modalStyle}>
                                <TextField label="Title" variant="outlined" sx={{ width: '100%', mb: 1 }}
                                    value={videoTitle} onChange={(e) => { setVideoTitle(e.target.value) }}
                                    inputProps={{ maxLength: 80 }}
                                />
                                <TextField label="Description" multiline variant="outlined" sx={{ width: '100%', mb: 1 }}
                                    value={videoDesc} onChange={(e) => { setVideoDesc(e.target.value) }}
                                    inputProps={{ maxLength: 800 }}
                                />




                                <Button variant="outlined" onClick={() => widgetRef.current.open()}>
                                    Add Video

                                </Button>
                                <Typography color="text.secondary" variant="body1" style={{ textAlign: 'justify' }}>
                                    {videoFileName}
                                </Typography>

                                {avatarPreview != '' &&
                                    <img style={{ width: '160px', height: '160px', display: 'block', margin: '6px 0' }} src={avatarPreview} />
                                }

                                {
                                    progressBar === true &&
                                    <div style={{ width: '100%', textAlign: 'center' }}>
                                        <CircularProgress />
                                    </div>
                                }

                                <div style={{ width: '100%', textAlign: 'right' }}>
                                    <Button disabled={progressBar}
                                        variant="contained" onClick={() => { uploadNewVideo(videoIndex) }}>
                                        Edit
                                    </Button>
                                </div>
                            </Box>
                        </Modal>



                        {/* Video Edit Model */}
                        {/* <Modal
                            open={openPaymentModal}
                            onClose={() => setOpenPaymentModal(false)}
                            aria-labelledby="modal-modal-title"
                            aria-describedby="modal-modal-description"
                            
                        >
                            <Box sx={modalStyle} style={{heigh:'400px'}}>
                                <Elements stripe={stripePromise}>
                                    <CheckoutForm  user={user}
                                    id={match.params.id} price={courseDetail?.price} url={courseDetail?.images?.url} title={courseDetail?.title} />
                                </Elements>
                            </Box>
                        </Modal> */}






                    </Container>
                </div>
            </div>

        </>
    )
}

export default VideoDetailForBuyer