import React, { useState, useEffect, useRef } from 'react'
import NavbarSeller from '../../layout/Header/NavbarSeller'
import MetaData from '../../layout/MetaData'
import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Button from '@mui/material/Button';
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import FormControl from '@mui/material/FormControl';
import OutlinedInput from '@mui/material/OutlinedInput';
import InputLabel from '@mui/material/InputLabel';
import InputAdornment from '@mui/material/InputAdornment';
import Stack from '@mui/material/Stack';
import { Cancel, Tag } from "@mui/icons-material";
import axios from 'axios'
import { useAlert } from "react-alert";
import CircularProgress from '@mui/material/CircularProgress';
import { useSelector } from 'react-redux'
import VideoCard from './VideoCard';
import Tags from './Tags'




const VideoCourse = () => {
    const [tabValue, setTabValue] = useState(0);
    const [open, setOpen] = useState(false);
    const avatarPickImage = useRef(null);
    const [title, setTitle] = useState('');
    const [desc, setDesc] = useState('');
    const [price, setPrice] = useState('');
    const [tags, setTags] = useState([]);
    const tagRef = useRef();
    const [avatar, setAvatar] = useState('');
    const [avatarPreview, setAvatarPreview] = useState("");
    const alert = useAlert();
    const [progressBar, setProgressBar] = useState(false)
    const { user } = useSelector(state => state.user)
    const [myVideos, setMyVideos] = useState([])

    useEffect(() => {
        getOwnVideos()


    }, [])

    const getOwnVideos = ()=>{
        setProgressBar(true)

        axios.get('/api/v1/video/me')
            .then(response => {
                console.log(response)
                setProgressBar(false)
                setMyVideos(response.data.videoCourses)
            })
            .catch(err => {
                setProgressBar(false)
                console.log(err)
                alert.error('Network error')
            })
    }




    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);
    function a11yProps(index) {
        return {
            id: `simple-tab-${index}`,
            'aria-controls': `simple-tabpanel-${index}`,
        };
    }
    const handleChange = (event, newValue) => {
        console.log(newValue)
        setTabValue(newValue);

        if(newValue === 0) getOwnVideos()
        

    };
    const handleDelete = (value) => {
        const newtags = tags.filter((val) => val !== value);
        setTags(newtags);
    };
    const handleOnSubmit = (e) => {
        e.preventDefault();
        setTags([...tags, tagRef.current.value.trim().replaceAll(' ', '_')]);
        tagRef.current.value = "";
    };

    const imagePick = (e) => {
        const reader = new FileReader();
        if (e.target.files[0]) {
            setAvatar(e.target.files[0]);
        }


        reader.onload = () => {
            if (reader.readyState === 2) {
                setAvatarPreview(reader.result);
                // setAvatar(reader.result);
            }
        };

        reader.readAsDataURL(e.target.files[0]);
    };

    const submitVideoCourse = () => {
        console.log(tags.map(item => item.replaceAll("_", ' ').toLowerCase()))
        if (title.trim() === '' || title.trim().length > 80) {
            alert.error('Please enter correct title...')
            return
        }
        else if (desc.trim() === '' || desc.trim().length > 80) {
            alert.error('Please enter correct description...')
            return
        }
        else if (price.trim() === '' || isNaN(parseInt(price)) || parseInt(price) <= 0) {
            alert.error('Please enter correct amount...')
            return
        }
        else if (tags.length <= 0) {
            alert.error('Please enter some tags...')
            return
        }
        else if (avatar === '' || avatarPreview === '') {
            alert.error('Please select title image...')
            return
        }

        console.log("best")
        setProgressBar(true)
        const myForm = new FormData();
        myForm.append("courseImage", avatar);
        myForm.append("title", title);
        myForm.append("description", desc);
        myForm.append("price", parseInt(price));
        myForm.append("searchTags", JSON.stringify(tags.map(item => item.replaceAll("_", ' ').toLowerCase())));

        const config = { headers: { "Content-Type": 'multipart/form-data' } };

        axios.post('/api/v1/video/new', myForm, config)
            .then(response => {
                console.log(response);
                setProgressBar(false)
                handleClose()
                setMyVideos(pre=> [...pre, response.data.videoCourse])
                setTitle('')
                setDesc('')
                setPrice('')
                setTags([])
                setAvatar('')
                setAvatarPreview('')
                alert.success("Created successfully")
            })
            .catch(err => {
                setProgressBar(false)
                console.log(err)
                alert.error(err.response.data.message)
            })

    }

    const modalStyle = {
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: 600,
        bgcolor: 'background.paper',
        borderRadius: '4px',
        boxShadow: 24,
        p: 4,
        overflowY: 'auto',


    };


    return (
        <>
            <MetaData title="Videos" />
            <NavbarSeller />

            <div className="dashboard">

                <div className='dashboard-inner' >


                    <Container maxWidth="lg" >
                        <span className="dashboard-title" >Videos</span>

                        <Grid container spacing={2} sx={{ mt: 2, mb: 1 }}>
                            <Grid item xs={8} md={8}>
                                <Tabs value={tabValue} onChange={handleChange} aria-label="course's tab">
                                    <Tab label="Video Courses" {...a11yProps(0)} />
                                    <Tab label="Purchased Courses" {...a11yProps(1)} />
                                </Tabs>
                            </Grid>
                            {tabValue <= 0 &&

                                <Grid item xs={8} md={4} textAlign={'right'} >
                                    <Button onClick={handleOpen} variant="contained" style={{ backgroundColor: 'teal' }}>ADD NEW COURSE</Button>
                                </Grid>
                            }
                        </Grid>

                        {
                            progressBar === true &&
                            <div style={{ width: '100%', textAlign: 'center' }}>
                                <CircularProgress />
                            </div>
                        }

                        <Grid container spacing={2} sx={{ my: 2 }}>

                            {
                                tabValue <= 0 ?
                                    myVideos.map((item, index) => (
                                        <Grid item xs={12} sm={6} md={3} key={index}>
                                            <VideoCard id={item?._id} url={item?.images?.url} title={item?.title} desc={item?.description} />
                                        </Grid>
                                        ))
                                    :
                                    <Grid item xs={12} sm={6} md={3}>
                                        <div>Hello 2</div>
                                    </Grid>
                            }



                        </Grid>




                        <Modal
                            open={open}
                            onClose={handleClose}
                            aria-labelledby="modal-modal-title"
                            aria-describedby="modal-modal-description"
                        >
                            <Box sx={modalStyle}>
                                <TextField label="Title" variant="outlined" sx={{ width: '100%', mb: 1 }}
                                    value={title} onChange={(e) => { setTitle(e.target.value) }}
                                    inputProps={{ maxLength: 80 }}
                                />
                                <TextField label="Description" multiline variant="outlined" sx={{ width: '100%', mb: 1 }}
                                    value={desc} onChange={(e) => { setDesc(e.target.value) }}
                                    inputProps={{ maxLength: 800 }}
                                />

                                {/* Amount */}
                                <FormControl fullWidth sx={{ my: 1 }}>
                                    <InputLabel htmlFor="outlined-adornment-amount">Amount</InputLabel>
                                    <OutlinedInput
                                        id="outlined-adornment-amount"
                                        startAdornment={<InputAdornment position="start">$</InputAdornment>}
                                        label="Amount"
                                        inputProps={{ maxLength: 5 }}
                                        value={price} onChange={(e) => { setPrice(e.target.value) }}
                                    />
                                </FormControl>

                                {/* Tags */}
                                <Box sx={{ flexGrow: 1 }}>
                                    <form onSubmit={handleOnSubmit}>
                                        <TextField
                                            inputRef={tagRef}
                                            fullWidth
                                            variant="outlined"
                                            sx={{ mb: 1, mt: 1 }}
                                            margin='none'
                                            label={tags.length < 5 ? "Tags" : ""}
                                            InputProps={{
                                                startAdornment: (
                                                    <Box sx={{ margin: "0 0.2rem 0 0", display: "flex", flexWrap: 'nowrap', }}>
                                                        {tags.map((data, index) => {
                                                            return (
                                                                <Tags data={data} handleDelete={handleDelete} key={index} />
                                                            );
                                                        })}
                                                    </Box>
                                                ),
                                            }}
                                        />
                                    </form>
                                </Box>


                                <Button variant="outlined" onClick={() => avatarPickImage.current.click()}>
                                    ADD TITLE IMAGE
                                    <input type="file"
                                        hidden ref={avatarPickImage}
                                        onChange={imagePick}
                                        accept=".jpg, .png, .jpeg, .gif|image/*" />
                                </Button>

                                {avatarPreview != '' &&
                                    <img style={{ width: '160px', height: '160px', display: 'block', margin: '6px 0' }} src={avatarPreview} />
                                }

                                {
                                    progressBar === true &&
                                    <div style={{ width: '100%', textAlign: 'center' }}>
                                        <CircularProgress />
                                    </div>
                                }

                                <div style={{ width: '100%', textAlign: 'right' }}>
                                    <Button disabled={progressBar}
                                        variant="contained" onClick={() => { submitVideoCourse() }}>
                                        CONTINUE
                                    </Button>
                                </div>
                            </Box>
                        </Modal>

                    </Container>



                </div>
            </div>

        </>
    )
}

export default VideoCourse