import {
  LOAD_GIG_REQUEST,
  LOAD_GIG_SUCCESS,
  LOAD_GIG_FAIL,
  CREATE_GIG_REQUEST,
  CREATE_GIG_SUCCESS,
  CREATE_GIG_FAIL,
  UPDATE_GIG_REQUEST,
  UPDATE_GIG_SUCCESS,
  UPDATE_GIG_FAIL,
  GIG_DETAILS_REQUEST,
  GIG_DETAILS_SUCCESS,
  GIG_DETAILS_FAIL,
  ALL_GIGS_REQUEST,
  ALL_GIGS_SUCCESS,
  ALL_GIGS_FAIL,
  CLEAR_ERRORS
} from '../constants/gigConstants'


import axios from "axios";
const BASE_URL = ''


export const loadGig = () => async (dispatch) => {
  try {
    dispatch({ type: LOAD_GIG_REQUEST });

    const { data } = await axios.get(`${BASE_URL}/api/v1/gig/me`);

    dispatch({ type: LOAD_GIG_SUCCESS, payload: data.gig });
  } catch (error) {
    dispatch({ type: LOAD_GIG_FAIL, payload: error.response.data.message });
  }
}

//Create GIG
export const createGig = (gigData) => async (dispatch) => {

  try {
    dispatch({ type: CREATE_GIG_REQUEST });
    const config = { headers: { "Content-Type": "application/json" } };
    const { data } = await axios.post(`${BASE_URL}/api/v1/gig/new`, gigData, config);

    dispatch({ type: CREATE_GIG_SUCCESS, payload: data.success, id: data.gigId });
  }
  catch (error) {
    dispatch({ type: CREATE_GIG_FAIL, payload: error.response.data.message });
  }
}

//Update Gig

export const updateGig = (gigData,id,p) => async (dispatch) => {
    try{
      dispatch({ type: UPDATE_GIG_REQUEST });
      let headerValue = "application/json"
      if(p==3)
          headerValue = "multipart/form-data"

      const config = { headers: { "Content-Type":headerValue  } };

      const { data } = await axios.put(`${BASE_URL}/api/v1/gig/${id}?p=${p}`, gigData, config);
      dispatch({ type: UPDATE_GIG_SUCCESS, payload: data.success, });


    }catch(error){
      dispatch({ type: UPDATE_GIG_FAIL, payload: error.response.data.message });

    }
}


// Detail Gig Data
export const detailGig = (id,userRequired)=> async (dispatch) => {

  try{
    dispatch({ type: GIG_DETAILS_REQUEST });

    const { data } = await axios.get(`${BASE_URL}/api/v1/gigDetail/${id}?user=${userRequired}`);

    dispatch({ type: GIG_DETAILS_SUCCESS, payload: data.data });


  }catch(error){
    dispatch({ type: GIG_DETAILS_FAIL, payload: error.response.data.message });

  }

}


// All Gigs Data
export const all_Gigs = (key,value)=> async (dispatch) => {

  try{
    dispatch({ type: ALL_GIGS_REQUEST });
    let link = `${BASE_URL}/api/v1/allGigs`;
    if(key && value)
      link += `?${key}=${value}`;

    const { data } = await axios.get(link);

    dispatch({ type: ALL_GIGS_SUCCESS, payload: data.data });


  }catch(error){
    dispatch({ type: ALL_GIGS_FAIL, payload: error.response.data.message });

  }

}

// Clearing Errors
export const clearErrors = () => async (dispatch) => {
  dispatch({ type: CLEAR_ERRORS });
};