import React, { useEffect, useState } from 'react'
import './CreateGig.css'
import { useParams, useLocation } from 'react-router-dom'
import MetaData from '../../layout/MetaData'
import { useDispatch, useSelector } from 'react-redux';
import { changeNavbar } from '../../../redux/actions/userAction'
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import Overview from './Overview';
import Pricing from './Pricing';
import Description from './Description';
import Gallery from './Gallery';
import Publish from './Publish';
import { useAlert } from "react-alert";
import { loadGig, createGig, updateGig, clearErrors, detailGig } from '../../../redux/actions/gigAction'
import { CREATE_GIG_RESET, UPDATE_GIG_RESET, CREATE_GIGID_RESET } from '../../../redux/constants/gigConstants';
import './CreateGig.css'
import Loader from '../../layout/Loader/Loader';
import NavbarSeller from '../../layout/Header/NavbarSeller'



// import { useHistory } from 'react-router-dom';
const CreateGig = ({ history }) => {
    const params = useParams();
    let { id } = params;
    const location = useLocation();
    const { pathname } = location;
    console.log(location.pathname);
    const [tabs, setTabs] = useState(id ? -1 : 0)
    const dispatch = useDispatch();
    const alert = useAlert();


    const { loading, isCreated, error, gigId } = useSelector(state => state.createGig)
    const { error: error1, isUpdated } = useSelector(state => state.updateGig)
    const { detailedGig, error: error2, loading: loading2 } = useSelector((state) => state.gigDetail)


    const setTabsByCallBack = (number, data) => {
        if (id && number == 0) {
            console.log('update ' + 0);
            dispatch(updateGig(data, id ? id : gigId, number))
        }
        else if (number == 0) {
            console.log('create' + 0);
            dispatch(createGig(data))
        }
        else if (number == 1) {
            console.log('update ' + 1);

            dispatch(updateGig(data, id ? id : gigId, number))
        }
        else if (number == 2) {
            console.log('update ' + 2);

            dispatch(updateGig(data, id ? id : gigId, number))
        }
        else if (number == 3) {
            console.log('update ' + 3);

            setTabs(tabs + 1);
        }
        else if (number == 4) {
            console.log('update ' + 4);

            dispatch(updateGig(data, id ? id : gigId, number))
        }
    }

    useEffect(() => {


        dispatch(changeNavbar('seller'))
        console.log("para " + id);
        console.log("create " + gigId);
        if (id || gigId) {
            console.log(id);
            dispatch(detailGig(id ? id : gigId, false));
        }




        if (isCreated) {
            // dispatch(loadGig())
            setTabs(tabs + 1);
            // dispatch(detailGig(id?id:gigId,false));
            alert.success("Data added successfully...")
            dispatch({
                type: CREATE_GIG_RESET
            })
        }

        if (isUpdated) {
            dispatch(loadGig())
            setTabs(tabs + 1);

            if (tabs < 3)
                alert.success("Data added successfully...")

            dispatch({
                type: UPDATE_GIG_RESET
            })

            if (tabs >= 4) {
                alert.success("Congratulations for Your Gig...")
                history.push('/Profile')
            }
        }

        if (error) {
            alert.error(error);
            dispatch(clearErrors());
        }
        if (error1) {
            alert.error(error);
            dispatch(clearErrors());
        }
        if (error2) {
            alert.error(error);
            dispatch(clearErrors());
        }
        else if (id) {
            setTabs(tabs + 1);
        }

        return () => {

        }
    }, [dispatch, alert, isCreated, error, loading, isUpdated, id])



    return (
        <>
            {
                (loading || loading2) ?
                    <Loader /> :
                    <>
                        <MetaData title="Create New" />
                        <NavbarSeller/>
                        <div className="settings">
                            <div className='settings-inner'>
                                <span className="settings-title" >Settings</span>


                                <div className="settings-main">

                                    <div className='settings-main-left'>
                                        <span className={tabs >= 0 ? 'active-setting' : ''}>
                                            {tabs > 0 && <CheckCircleIcon style={{ position: 'absolute', left: '8px' }} />}Overview</span>
                                        <span className={tabs >= 1 ? 'active-setting' : ''}>
                                            {tabs > 1 && <CheckCircleIcon style={{ position: 'absolute', left: '8px' }} />}Pricing</span>
                                        <span className={tabs >= 2 ? 'active-setting' : ''}>
                                            {tabs > 2 && <CheckCircleIcon style={{ position: 'absolute', left: '8px' }} />}Description</span>
                                        <span className={tabs >= 3 ? 'active-setting' : ''}>
                                            {tabs > 3 && <CheckCircleIcon style={{ position: 'absolute', left: '8px' }} />}Gallery</span>
                                        <span className={tabs >= 4 ? 'active-setting' : ''}>
                                            {tabs > 4 && <CheckCircleIcon style={{ position: 'absolute', left: '8px' }} />}Publish</span>
                                    </div>
                                    <div className='settings-main-right'>
                                        {
                                            tabs == 0 ? <Overview gigId={id} data={!pathname.includes('new') && detailedGig} click={setTabsByCallBack} /> :
                                                tabs == 1 ? <Pricing gigId={id ? id : gigId} data={!pathname.includes('new') && detailedGig} click={setTabsByCallBack} /> :
                                                    tabs == 2 ? <Description gigId={id ? id : gigId} data={!pathname.includes('new') && detailedGig} click={setTabsByCallBack} /> :
                                                        tabs == 3 ? <Gallery gigId={id ? id : gigId} data={!pathname.includes('new') && detailedGig} click={setTabsByCallBack} /> :
                                                            <Publish gigId={id ? id : gigId} click={setTabsByCallBack} />

                                        }
                                    </div>
                                </div>
                            </div>
                        </div>

                    </>
            }

        </>
    )
}

export default CreateGig