import React, { useState, useEffect, useRef } from 'react'
import NavbarBuyer from '../../layout/Header/NavbarBuyer'
import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Divider from '@mui/material/Divider';
import FormControl from '@mui/material/FormControl';
import OutlinedInput from '@mui/material/OutlinedInput';
import InputLabel from '@mui/material/InputLabel';
import InputAdornment from '@mui/material/InputAdornment';
import CircularProgress from '@mui/material/CircularProgress';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import Avatar from '@mui/material/Avatar';
import ImageIcon from '@mui/icons-material/Image';
import Rating from '@mui/material/Rating';
import StarIcon from '@mui/icons-material/Star';
import { Link } from "react-router-dom";

import { useAlert } from "react-alert";
import axios from 'axios'
const labels = {
    0: 'Useless',
    0.5: 'Useless',
    1: 'Useless+',
    1.5: 'Poor',
    2: 'Poor+',
    2.5: 'Ok',
    3: 'Ok+',
    3.5: 'Good',
    4: 'Good+',
    4.5: 'Excellent',
    5: 'Excellent+',
};
const SearchVideoPage = () => {

    const [text, setText] = useState('')
    const [videosData, setVideosData] = useState([])
    const [progressBar, setProgressBar] = useState(false)

    const handleOnSubmit = (e) => {
        e.preventDefault()
        if (text.trim() === '') {
            return
        }
        setVideosData([])
        setProgressBar(true)
        axios.get(`/api/v1/searchVideosByKeywords?keywords=${text}`)
            .then(res => {
                setProgressBar(false)
                console.log(res.data)
                setVideosData(res.data.videoCourses)
            })
            .catch(err => {
                setProgressBar(false)
                alert.error("Something went wrong")
            })

    }

    return (
        <>
            <NavbarBuyer />

            <div>
                <div className='premium-hero'>
                    <div className='top'>Freelancers</div>
                    <div className='mid'>Search Expert's Vidoes to get New Skills</div>
                    <div className='bottom'>Looking for top quality Skill? WorkPay has you covered.</div>
                    <div className='premiumTextAreaContainer'>

                    </div>
                </div>
            </div>

            <Container maxWidth="lg" >
                <Grid container sx={{ alignItems: "center", justifyContent: 'center', my: 3 }}>
                    <Grid item xs={12} sm={8} md={8} >
                        <form onSubmit={handleOnSubmit}>
                            <TextField id="outlined-basic"
                                label="Search Videos" variant="outlined"
                                sx={{ width: '100%', mb: 1 }}
                                value={text}
                                onChange={(e) => { setText(e.target.value) }}

                            />
                        </form>
                    </Grid>

                    {
                        progressBar === true &&
                        <div style={{ width: '100%', textAlign: 'center' }}>
                            <CircularProgress />
                        </div>
                    }

                    {
                        videosData?.map((item, index) => (
                            <Grid spacing={2} item xs={10} sx={{my:2}} key={index}>
                            <Link to={`/videoDetailForBuyer/${item._id}`} style={{textDecoration:'none'}}>

                                <ListItem style={{textAlign:'flex-start'}}>
                                    <ListItemAvatar>
                                        <img src={item?.images?.url}
                                            style={{ width: '200px', height: '100px', borderRadius:'4px',boxShadow:'2px 2px 4px black' }}
                                        />
                                    </ListItemAvatar>
                                    <ListItemText style={{ marginLeft: '10px',textAlign:'flex-start' }}
                                        primary={item?.title} secondary={item?.description} />
                                    <Box
                                        sx={{
                                            width: 200,
                                            display: 'flex',
                                            alignItems: 'center',
                                        }}
                                    >
                                        <Rating
                                            name="text-feedback"
                                            value={3.5}
                                            readOnly
                                            precision={0.5}
                                            emptyIcon={<StarIcon style={{ opacity: 0.55 }} fontSize="inherit" />}
                                        />
                                        <Box sx={{ ml: 2 }}>{labels[3.5]}</Box>
                                    </Box>
                                </ListItem>
                                <Divider  style={{width:'100%'}} />
                                </Link>
                            </Grid>
                        ))
                    }


                </Grid>
            </Container>

        </>
    )
}

export default SearchVideoPage