import * as React from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import ReactPlayer from 'react-player/file'

export default function SingleVideoCard({title,desc,url,index,id,public_id,openVideoEdit,openDeleteVideo}) {
  return (
    <Card style={{minHeight:'400px',position:'relative'}}>
      <ReactPlayer
        height={240}
        width={'100%'}
        url={url}
        controls={true} 
        light={true}
        style={{backgroundColor:'black',}}
        config={{
          file:{
            attributes:{controlslist:"nodownload"}
          }
        }}        

      />
      <CardContent>
        <Typography gutterBottom variant="h5" component="div">
          {title}
        </Typography>
        <Typography variant="body2" color="text.secondary">
          {desc}
        </Typography>
      </CardContent>
      <CardActions  sx={{mt:'auto'}} style={{position:'absolute',bottom:'6px',right:'6px'}}>
        <Button size="small" variant="outlined" onClick={openVideoEdit}>Edit</Button>
        <Button size="small" variant="outlined" color="error" onClick={openDeleteVideo}>Delete</Button>
      </CardActions>
    </Card>
  );
}