import React,{useState} from 'react'
import './OrderPlace.css'
import NavbarBuyer from '../../layout/Header/NavbarBuyer'
import { useDispatch, useSelector } from 'react-redux'
import { Link,useHistory } from 'react-router-dom'
import { useAlert } from "react-alert";
import axios from 'axios'
const OrderPlace = (props) => {
  const alert = useAlert();
  const history = useHistory();


  const { user } = useSelector(state => state.user)
  const [text, setText] = useState('')
  const tabs = props.match.params.id
  const state = props.location?.state


  const handleRequirements = ()=>{
    if(text.trim().length< 30){
      alert.error('Please write atleast 30 characters...')
    }
    else{
      let obj = {
        price:{
          amount:state.gig.price[tabs]?.amount,
          name:state.gig.price[tabs]?.name,
          description:state.gig.price[tabs]?.description,
          deliveryDays:state.gig.price[tabs]?.deliveryDays,
        },
        description:text,
        userTo:{
          id:state.user._id,
          name:state.user.name
        },
        gigId:{
          id:state.gig._id,
          image:state.gig.images.url
        },
        status:'ACTIVE',
      } 

      axios.post('/api/v1/orderPlace/new',obj)
      .then(response => {
        console.log(response)
        const data = response.data.order
        alert.success("Order Place Successfully!")
        setText('')
        history.push(`/OrderDetail?order=${data._id}&&userTo=${data.userTo.id}&&gigId=${data.gigId.id}`)
      })
      .catch(error => {
        console.log(error)
        alert.error("Error in Order Placing!")

      })



    }
  }



  return (
    <>
      <NavbarBuyer />
      {
        state ?
          <>
            <div className='orderPlace'>

              <div className='left'>
                <div className='gigSection'>
                  <div className='left' >
                    <div>
                      <img src={state?.gig?.images?.url} alt="pic" className='gigImage' />
                    </div>
                    <div className='little-info'>

                      <div className="gigTitle">{state.gig.title}</div>
                      <div style={{ display: 'flex', alignItems: 'center' }}>
                        <div className="stars-div">
                          <i className="fa fa-star" aria-hidden="true"> </i>
                          <i className="fa fa-star" aria-hidden="true"> </i>
                          <i className="fa fa-star" aria-hidden="true"> </i>
                          <i className="fa fa-star" aria-hidden="true"> </i>
                          <i className="fa fa-star" aria-hidden="true"> </i>
                        </div>
                        <div className='rating-container'>
                          5.{state.gig.ratings}<span style={{ color: 'grey' }}>({state.gig.numOfReviews} Reviews)</span>
                        </div>
                      </div>
                    </div>



                  </div>
                  <div className='right'>US${state.gig.price[tabs]?.amount}</div>
                </div>





                <div className='requirementsTextAreaContainer'>
                    <div className='premiumTextAreaTop'>Write Your complete project's Requirements here...</div>
                    <textarea type="text" name="text" rows="10" value={text} className='premiumTextArea' placeholder="Write Your complete project's requirements here..."
                        onChange={(e) => setText(e.target.value)}
                    />
                    {/* <div className='buttonToSearch'>
                        <span onClick={handleFreelancers}>Search</span>
                    </div> */}
                </div>

              </div>



              <div className='right'>

                <div className='checkoutContainer'>
                  <div className='title'>Price Summary</div>
                  <div className='mid'>
                    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', margin: '10px 0' }}>
                      <span className='child'>Subtotal</span>
                      <span className='child'>US${state.gig.price[tabs]?.amount}</span>
                    </div>
                    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', margin: '10px 0' }}>
                      <span className='child'>Service Fee</span>
                      <span className='child'>US${2.28}</span>
                    </div>

                  </div>
                  <div className='bottom'>
                    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', margin: '10px 0' }}>
                      <span className='child1'>Total</span>
                      <span className='child1'>US${state.gig.price[tabs]?.amount + 2.28}</span>
                    </div>
                    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', margin: '10px 0' }}>
                      <span className='child'>Delivery Time</span>
                      <span className='child'>{state.gig.price[tabs]?.deliveryDays.substring(0, 7)}</span>
                    </div>
                  </div>

                    <div style={{display:'flex',justifyContent:'center'}}>
                    <div className='orderPlaceButton'
                      onClick={handleRequirements}
                      >
                      Place Order</div>
                    </div>
                  


                </div>

              </div>

            </div>
          </>
          :
          <div>Sorry! You can not place order</div>
      }

    </>
  )
}

export default OrderPlace