import React from 'react'
import SearchIcon from '@mui/icons-material/Search';
import { Link } from 'react-router-dom'
import './PremiumSearchButton.css'
const PremiumSearchButton = () => {
    return (
        <>

            <Link to={`/premiumSearch`} className={`premiumButtonContaier animation-entering`}>
                <div style={{ color: 'teal', fontWeight: 'bold', fontSize: '20px' }}>Premium Search</div>
                <SearchIcon style={{ color: 'teal', marginLeft: '6px', height: '30px', width: '30px', marginTop: '4px' }} />
            </Link>


        </>
    )
}

export default PremiumSearchButton