import React, { Fragment, useEffect } from "react";
import { CgMouse } from "react-icons/all";
import "./Home.css";
import GigsCard from "./GigsCard.js";
import MetaData from "../../layout/MetaData";
import { useSelector, useDispatch } from "react-redux";
import Loader from "../../layout/Loader/Loader";
import { useAlert } from "react-alert";
import happyPerson from '../../../images/face5.png'
import { changeNavbar } from '../../../redux/actions/userAction'
import { all_Gigs, clearErrors } from '../../../redux/actions/gigAction'
import SearchField from "./SearchField";
import NavbarBuyer from '../../layout/Header/NavbarBuyer'
import Button from '@mui/material/Button';
import { Link } from "react-router-dom";

const Home = () => {
  const alert = useAlert();
  const dispatch = useDispatch();
  const { loading, error, } = useSelector((state) => state.user);
  const { detailedGig, error: error2, loading: loading2 } = useSelector(state => state.gigDetail)

  useEffect(() => {
    dispatch(changeNavbar('buyer'))
    if (error) {
      // alert.error(error);
      dispatch(clearErrors());

    }
    dispatch(all_Gigs())
  }, [dispatch, error, alert]);

  return (
    <Fragment>
      <NavbarBuyer />
      {
        loading || loading2 ? (
          <Loader />
        ) : (
          <Fragment>
            <MetaData title="WorkPay" />

            <div className="banner">
              <Link to={`/searchVideoPage`} style={{ textDecoration: 'none' }}>

                <Button variant="outlined" color="secondary">Search Videos</Button>
              </Link>

              <SearchField />
              <p>Welcome to WorkPay</p>
              <h1>{"Get Ready for your pending projects".toUpperCase()}</h1>

              <a href="#container">
                <button >
                  Scroll
                  <CgMouse style={{ marginLeft: '4px', }} />
                </button>
              </a>
              <img src={happyPerson} alt="face" className="bannerImage" />
            </div>

            {/* <div className="homeHeadingContainer"> */}
            <h2 className="homeHeading">Featured Gigs</h2>
            {/* </div> */}
            {
              detailedGig?.gig && detailedGig.userData &&
              <div className="container" id="container">

                {
                  detailedGig?.gig?.map((item, index) => (
                    <GigsCard key={index} gig={item} user={detailedGig.userData[index]} />
                  ))
                }

              </div>
            }

          </Fragment>
        )}
    </Fragment>
  );
};

export default Home;
