import React from 'react'
import {useDispatch} from 'react-redux'
const Publish = ({gigId,click}) => {
  const dispatch = useDispatch();
  const checkFormAndSubmit = ()=>{
      dispatch({
        type:"CREATE_GIGID_RESET"
      })
      click(4,{draft:false})
  }
  return (
    <div className="publish-container">
        <div className="title">Almost there...</div>
        <div className="desc" >Let's publish your Gig and get some buyers rolling in.</div>
        <div className="save-button" style={{marginTop:'40px'}} onClick={checkFormAndSubmit}>
          Publish
        </div>
    </div>
  )
}

export default Publish