import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useAlert } from 'react-alert'
import Loader from '../../layout/Loader/Loader'
import { detailGig, clearErrors } from '../../../redux/actions/gigAction'
import MetaData from '../../layout/MetaData'
import { changeNavbar } from '../../../redux/actions/userAction'
import './GigDetail.css'
import { Link, } from 'react-router-dom'
import Avatar from '@mui/material/Avatar';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import NotFound from '../../layout/Not Found/NotFound'
import { GIG_DETAILS_RESET } from '../../../redux/constants/gigConstants';
import NavbarBuyer from '../../layout/Header/NavbarBuyer'


const GigDetail = ({ match }) => {
    const { loading, detailedGig, error } = useSelector((state) => state.gigDetail)
    const { user } = useSelector(state => state.user)
    const [tabs, setTabs] = useState(0)
    const dispatch = useDispatch()
    const alert = useAlert();
    useEffect(() => {

        // window.addEventListener('popstate', () =>{
        //         dispatch({
        //             type:GIG_DETAILS_RESET
        //         })
        // })
        console.log(match.params);
        console.log(user);
        // console.log('Hello1')
        console.log(detailedGig)

        if (error) {
            alert.error(error);
            dispatch(clearErrors());
        }

        dispatch(detailGig(match.params.id, true))

    }, [dispatch, error])


    const handleOrderPurchase = () => {
        console.log(detailedGig);
    }




    return (
        <>
            <NavbarBuyer />
            {
                loading ?
                    <Loader /> :
                    <>
                        {
                            detailedGig?.gig && detailedGig?.user ?

                                <>
                                    <MetaData title={detailedGig?.gig?.title} />

                                    <div className="detail-container">
                                        <div className="detail-inner">
                                            {
                                                user?._id === detailedGig.user._id &&
                                                <div className="top">
                                                    <Link to={`/gig/edit/${detailedGig.gig._id}`} className="edit-link">
                                                        Edit this Gig
                                                    </Link>
                                                </div>
                                            }


                                            <div className='header-section'>
                                                <div className='left-container'>
                                                    <div className="title">{detailedGig.gig.title}</div>
                                                    <div className='little-info'>
                                                        <Avatar src={detailedGig.user.avatar?.url ?
                                                            detailedGig.user.avatar.url : "./Profile.png"} />

                                                        <span>{detailedGig.user.name}</span>
                                                        | GIG REVIEWS
                                                        <div className="stars-div">
                                                            <i className="fa fa-star" aria-hidden="true"> </i>
                                                            <i className="fa fa-star" aria-hidden="true"> </i>
                                                            <i className="fa fa-star" aria-hidden="true"> </i>
                                                            <i className="fa fa-star" aria-hidden="true"> </i>
                                                            <i className="fa fa-star" aria-hidden="true"> </i>
                                                        </div>
                                                        <div className='rating-container'>
                                                            {detailedGig.gig.ratings} ({detailedGig.gig.numOfReviews})
                                                        </div>
                                                        <div className="sellerInfo-button">Contact Me</div>

                                                    </div>


                                                    <div className="image-container">
                                                        <img src={detailedGig.gig.images?.url
                                                            ? detailedGig.gig.images.url :
                                                            "https://assetsv2.fiverrcdn.com/assets/v2_globals/no-image-98a579918ef768aae1f4d7ec166a5471.svg"
                                                        } alt="" />
                                                    </div>

                                                    <div className="about-container">
                                                        <div className="about-top">About this Gig</div>
                                                        <div className="about-desc">{
                                                            detailedGig.gig?.description?.split(/\n\r?/g).map((item = ' ', index) => {

                                                                if (item.includes('https://')) {

                                                                    return item.split(' ').map((i, ind) => {
                                                                        if (i.startsWith('https://')) {
                                                                            return <span key={ind}><a href={i} target="_blank"> {i}</a> </span>
                                                                        }
                                                                        else
                                                                            return i + " "
                                                                    })

                                                                }
                                                                else
                                                                    return <div key={index} style={{ whiteSpace: 'pre-line' }}>{item}
                                                                        <span style={{ color: 'white' }}>.</span>
                                                                    </div>;
                                                            }
                                                            )
                                                        }

                                                        </div>
                                                    </div>


                                                    <div className="sellerInfo-container">
                                                        <div className="sellerInfo-top"> About the Seller</div>
                                                        <div className="sellerInfo-design">
                                                            <Avatar className="sellerInfo-image" src={detailedGig.user?.avatar?.url ? detailedGig.user.avatar.url :
                                                                'https://assetsv2.fiverrcdn.com/assets/v2_globals/no-image-98a579918ef768aae1f4d7ec166a5471.svg'} />
                                                            <div className="sellerInfo-right">
                                                                <div className="sellerInfo-name">{detailedGig.user?.name}</div>
                                                                <div className="sellerInfo-ratings">
                                                                    <i className="fa fa-star" aria-hidden="true"> </i>
                                                                    <i className="fa fa-star" aria-hidden="true"> </i>
                                                                    <i className="fa fa-star" aria-hidden="true"> </i>
                                                                    <i className="fa fa-star" aria-hidden="true"> </i>
                                                                    <i className="fa fa-star" aria-hidden="true"> </i>
                                                                    {detailedGig.user?.ratings}</div>
                                                                <div className="sellerInfo-button">Contact Me</div>
                                                            </div>
                                                        </div>
                                                    </div>




                                                    <div className="description-container">
                                                        <div className="description-container-top">
                                                            <div>Member Since: June 2022</div>
                                                            <div>Last Delivery: 11 Months</div>
                                                        </div>

                                                        <div className="description-container-bottom">
                                                            {detailedGig.user?.description}
                                                        </div>
                                                    </div>

                                                    <div className='tags-container'>
                                                        <div>TAGS</div>
                                                        {detailedGig.gig?.searchTags.map((item, index) => (
                                                            <span key={index}>{item}</span>
                                                        ))}
                                                    </div>

                                                </div>



                                                <div className='right-container'>
                                                    <div className="right-container-top">
                                                        <div onClick={() => setTabs(0)} className={tabs == 0 ? 'inner right-container-top-active' : 'inner'} >Basic</div>
                                                        <div onClick={() => setTabs(1)} className={tabs == 1 ? 'inner right-container-top-active' : 'inner'}>Standard</div>
                                                        <div onClick={() => setTabs(2)} className={tabs == 2 ? 'inner right-container-top-active' : 'inner'}>Premium</div>
                                                    </div>
                                                    <div>
                                                        <div className='middle'>
                                                            <div style={{display:'flex',justifyContent:'space-between',alignItems:'center'}}>
                                                                <div className='amount'>{tabs == 0 ? 'Basic' : tabs == 1 ? 'Standard' : "Premium"}</div>
                                                                <div className='amount'>US${detailedGig.gig.price[tabs]?.amount}</div>
                                                            </div>
                                                            <span className='name'>{detailedGig.gig.price[tabs]?.name}</span>
                                                            <span className='desc'>{detailedGig.gig.price[tabs]?.description}</span>
                                                        </div>

                                                        <div
                                                        className='bottom-container'>
                                                            <div style={{ display: 'flex', alignItems: 'center', width: '90%', marginBottom: '20px' }}><AccessTimeIcon /><div className='delivery'>{detailedGig.gig.price[tabs]?.deliveryDays}</div></div>
                                                            {
                                                                user?._id === detailedGig.user._id ?
                                                                <div  
                                                                className='button'
                                                                // onClick={handleOrderPurchase}
                                                                style={{
                                                                    backgroundColor: user?._id === detailedGig.user._id ? 'grey' : 'teal',
                                                                    cursor: user?._id === detailedGig.user._id ? 'default' : 'pointer'
                                                                }}
                                                            >Continue ( US${detailedGig.gig.price[tabs]?.amount} )</div>
                                                            :
                                                            <Link to={{pathname: `/orderPlace/${tabs}`,state:detailedGig}} 
                                                                className='button'
                                                                onClick={handleOrderPurchase}
                                                                style={{
                                                                    backgroundColor: user?._id === detailedGig.user._id ? 'grey' : 'teal',
                                                                    cursor: user?._id === detailedGig.user._id ? 'default' : 'pointer'
                                                                }}
                                                            >Continue ( US${detailedGig.gig.price[tabs]?.amount} )</Link>
                                                            }
                                                            
                                                            
                                                            
                                                            
                                                        </div>

                                                    </div>
                                                </div>
                                            </div>

                                        </div>
                                    </div>




                                </> : <>
                                    {/* <NotFound/> */}
                                </>
                        }
                    </>
            }
        </>
    )
}

export default GigDetail