import React,{useEffect} from 'react'
import "./App.css";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

import PremiumSearchButton from './components/layout/PremiumSearchButton/PremiumSearchButton'
import Footer from './components/layout/Footer/Footer'
import {
  LoginSignUp,
  Home,
  Dashboard,
  Settings,
  Gigs,
  Profile,
  CreateGig,
  GigDetail,
  PremiumSearch,
  OrderPlace,
  OrderDetail,
  VideoCourse,
  VideoCourseEdit,
  SearchVideoPage,
  VideoDetailForBuyer,
} from './components/Pages'
import NotFound from './components/layout/Not Found/NotFound'
import store from "./store";
import {clearErrors, loadUser,changeNavbar } from './redux/actions/userAction'
import { useDispatch, useSelector } from "react-redux";
import ProtectedRoute from './components/Route/ProtectedRoute'

const App = ()=>{
  const {  error } = useSelector((state) => state.user);
  const { navbarStatus } = useSelector((state) => state.navbar)
  useEffect(() => {
    store.dispatch(loadUser());
    store.dispatch(changeNavbar())
    if(error){
      store.dispatch(clearErrors());
    }
  }, [])
  

  return (
  <>
    {/* <Router> */}
    {/* { 
      navbarStatus === 'seller'?
        <NavbarSeller/>:
        <NavbarBuyer/>
    } */}
      
      <PremiumSearchButton/>
      <Switch>
       <Route exact path="/"  component={Home}/>
       <Route exact path="/login"  component={LoginSignUp}/>
       <Route exact path="/gigs"  component={Gigs}/>
       <Route exact path="/gig/:id" component={GigDetail} />
       <Route exact path="/premiumSearch"  component={PremiumSearch}/>
       <Route exact path="/searchVideoPage"  component={SearchVideoPage}/>
       <Route exact path="/videoDetailForBuyer/:id"  component={VideoDetailForBuyer}/>
       <ProtectedRoute exact path="/dashboard" component={Dashboard} />
       <ProtectedRoute exact path="/settings" component={Settings} />
       <ProtectedRoute exact path="/profile" component={Profile} />
       <ProtectedRoute exact path="/gig/new" component={CreateGig} />
       <ProtectedRoute exact path="/gig/edit/:id" component={CreateGig} />
       <ProtectedRoute exact path="/orderPlace/:id" component={OrderPlace} />
       <ProtectedRoute exact path="/OrderDetail" component={OrderDetail} />
       <ProtectedRoute exact path="/videoCourse" component={VideoCourse} />
       <ProtectedRoute exact path="/videoCourse/edit/:id" component={VideoCourseEdit} />
       <Route  path="*"  component={NotFound}/>

      </Switch>
    {/* </Router> */}
    <Footer/>
   </>
  )
}

export default App