import React, { useState, useEffect } from 'react'
import CancelIcon from '@mui/icons-material/Cancel';
import Loader from '../../layout/Loader/Loader';
import {useAlert} from 'react-alert';
import Select from 'react-select'
const Overview = ({ data, click }) => {
  const [tags, setTags] = useState(data? data?.gig?.searchTags:[''])
  let arr = [];
  const alert = useAlert();
  const [title, setTitle] = useState(data? data?.gig?.title:'')
  const [category, setCategory] = useState(data? data?.gig?.category:'')
  const [subCategory, setSubCategory] = useState(data? data?.gig?.subCategory:'')
  const categoryOptions = [
    {label:"Graphic Designing",value:'graphic'},
    {label:"Programming",value:'programming'},
    {label:"Music & Audio",value:'music'},
    {label:"Business",value:'business'},
  ]
  const subCategoryOptions = [
    {label:"Game Development",value:'game'},
    {label:"Mobile Apps",value:'mobile'},
    {label:"Websites",value:'websites'},
    {label:"Softwares",value:'softwares'},
  ]
 

  const handleTags = (e) => {
    arr = [...tags]
    arr[e.target.name] = e.target.value
    setTags([...arr])
    console.log(arr);
  }
  const addTags = (e) => {
    setTags([...tags, ''])
  }
  const removeTags = (i) => {
    setTags([...tags.filter((item, index) => index != i)])
    arr = tags.filter((item, index) => index != i)
  }
  const handleCategory = (e) => {
    if (e.target.value != 'programming')
      setSubCategory('')
    else
      setSubCategory('game')

    setCategory(e.target.value)
  }

  const checkFormAndSubmit = () => {
    if (title === '') {
      alert.error('Please Enter Title')
      return;
    }

    let filter = tags?.filter(item => item != '')

    if(category == ''){
      alert.error('Please enter category')
      return
    }
    else if (filter?.length == 0) {
      alert.error('Please add atleast one tag')
      return
    }
    
    const data = {
        title, category, subCategory, searchTags: filter
      }
      click(0, data)
    

  }

  return (
    <>
      
        <div className="overview-container">
          <div className="overview-inner">
            <label htmlFor="title">Title</label>
            <textarea type="text" id="title" name="title" value={title}
              rows="2" placeholder="I will..."
             onChange={(e) => setTitle(e.target.value)}

            />
          </div>

          <div className="overview-mid">
            <div style={{zIndex:2,position:'relative'}}>
              <label htmlFor="category">Category:</label>

              <Select options={categoryOptions} isSearchable={false} 
              defaultValue={()=>categoryOptions.filter(item=>item.value==category)[0]} 
              onChange={(e)=>setCategory(e.value)} />

            </div>
            {
              category == 'programming' ? <div style={{zIndex:2,position:'relative'}}>
                <label htmlFor="category">Sub category:</label>
                
                <Select options={subCategoryOptions} isSearchable={false} 
                defaultValue={()=>subCategoryOptions.filter(item=>item.value==subCategory)[0]} 
                onChange={(e)=>setSubCategory(e.value)} />

              </div> : <></>
            }
          </div>

          <div className="overview-bottom">
            <div>
              <label htmlFor="title">Tags  </label><span>(maximum 5)</span></div>
            {
               tags?.map((item, index) => (
                <span className="tagBox-container" key={index}>
                  <input type="text"
                    name={index} value={item} onChange={handleTags} />
                  {tags && tags.length > 1 ? <CancelIcon className="icon" onClick={() => removeTags(index)} /> : <></>}
                </span>
              ))
            }
            {
              tags && tags.length < 5 ?
                <span className="add-button" onClick={addTags}>Add</span>
                : <></>
            }

          </div>
          <div className="save-button" onClick={checkFormAndSubmit}>
            Save
          </div>
        </div>
    

    </>
  )
}

export default Overview