import React, { useEffect } from 'react'
import { useLocation } from 'react-router-dom'
import './Gigs.css'
import '../Home/Home.css'
import MetaData from "../../layout/MetaData";
import GigsCard from '../Home/GigsCard'
import { useDispatch, useSelector } from 'react-redux'
import { all_Gigs, clearErrors } from '../../../redux/actions/gigAction'
import Loader from '../../layout/Loader/Loader'
import SearchField from '../Home/SearchField'
import NavbarBuyer from '../../layout/Header/NavbarBuyer'



const Gigs = () => {
    const { search } = useLocation();
    const dispatch = useDispatch();
    const { detailedGig, error, loading } = useSelector(state => state.gigDetail)
    const preString = search?.substring(1, search.indexOf('='));
    const postString = search?.replaceAll('%20', ' ').replaceAll('-', ' ').substring(search.indexOf('=') + 1)
    const title = postString.toUpperCase();

    const checkingQuery = (pre = '', post = '') => {
        let key = '', value = "";
        if (pre?.toLowerCase() == 'category') {
            key = 'category'
            if (post?.toLowerCase().includes('graphic')) {
                value = 'graphic'
            }
            else if (post?.toLowerCase().includes('programming')) {
                value = 'programming'
            }
            else if (post?.toLowerCase().includes('music')) {
                value = 'music'
            }
            else if (post?.toLowerCase().includes('business')) {
                value = 'business'
            }
        }
        else if (pre?.toLowerCase() == 'subcategory') {
            key = 'subCategory'
            if (post?.toLowerCase().includes('game')) {
                value = 'game'
            }
            else if (post?.toLowerCase().includes('mobile')) {
                value = 'mobile'
            }
            else if (post?.toLowerCase().includes('websites')) {
                value = 'websites'
            }
            else if (post?.toLowerCase().includes('softwares')) {
                value = 'softwares'
            }
        }
        else if(pre?.toLowerCase() == 'keywords') {
            key = 'keywords'
            value = postString
            console.log(postString);
        }
        else{
            key=''
            value=''
        }
        

        return ({ key, value })
    }

    useEffect(() => {
        // if (search) {
            let { key, value } = checkingQuery(preString, postString);

            dispatch(all_Gigs(key, value))
            if(error){
                dispatch(clearErrors())
            }
        // }
    }, [search,dispatch,error,alert])
    return (

        <>
        <NavbarBuyer/>
            {
                loading ?
                    <Loader /> :
                    <>
                            <MetaData title={title} />
                            <SearchField/>
                        {
                            detailedGig?.gig?.length>0 ?
                                <>


                                    <div className="gigs">
                                        <div className='gigs-inner' >
                                            <span className="gigs-title" >{search && title}</span>

                                        </div>

                                        <div className="container" id="container">
                                            {
                                                detailedGig.gig.map((item, index) => (
                                                    <GigsCard key={index} gig = {item} user = {detailedGig.userData[index]}
                                                    />
                                                ))
                                            }

                                        </div>
                                    </div>
                                </> :
                                <>
                                <div className="gigs">
                                        <div className='gigs-inner' >
                                            <span className="gigs-title" >{search && title}</span>
                                            <div> No Record Found... </div>

                                        </div>
                                </div>        
                                
                                 </>
                        }
                    </>
            }
        </>

    )
}

export default Gigs