import React, { useState } from 'react'
import NavbarBuyer from '../../layout/Header/NavbarBuyer'
import './PremiumSearch.css'
import axios from 'axios'
import { useAlert } from "react-alert";
import Avatar from '@mui/material/Avatar';



const Loader = () => {
    return (
        <div className="loading2">
            <div></div>
        </div>
    );
};

const PremiumSearch = () => {
    const alert = useAlert();
    const [loading,setLoading] = useState(false)
    const URL = 'http://127.0.0.1:5000/premiumSearch'
    const [text, setText] = useState('')
    const [apiDate, setApiDate] = useState([])



    const handleFreelancers = () => {
        if (text.trim() === '' || text.trim().length < 10) {
            alert.error('Please write atleast 10 characters...')
        }
        else {
            setLoading(true)
            axios.post(URL, { searchString: text })
                .then(res => {
                    console.log(res);
                    setApiDate(res.data.data)
                    setLoading(false)
                })
                .catch(err => {
                    console.log(err)
                    alert.error('There is an error in fetchig data')
                    setLoading(false)
                })
        }
    }



    return (
        <>
            <NavbarBuyer />
            <div>


                <div className='premium-hero'>
                    <div className='top'>Freelancers</div>
                    <div className='mid'>Search Expert Freelancers for Any Project</div>
                    <div className='bottom'>Looking for top quality Freelance talent? WorkPay has you covered.</div>
                    <div className='premiumTextAreaContainer'>

                    </div>
                </div>

                <div className='premiumTextAreaContainer'>
                    <div className='premiumTextAreaTop'>Write Your complete project's description here...</div>
                    <textarea type="text" name="text" rows="10" value={text} className='premiumTextArea' placeholder="Write Your complete project's description here..."
                        onChange={(e) => setText(e.target.value)}
                    />
                    <div className='buttonToSearch'>
                        <span onClick={handleFreelancers}>Search</span>
                    </div>
                </div>


                { loading && <Loader /> }
                <div className="freelancersData">
                    <div className="freelancersDataTop">
                        PREMIUM FREELANCERS
                    </div>

                    {
                        (apiDate.length == 0 && loading==false) &&
                        <span> No Record found...</span>
                    }

                    <div className='dashboard-main-right' style={{marginTop:'10px'}}>
                        {
                            apiDate.length > 0 &&
                            apiDate.map((item, index) => (
                                <div key={index} className='order-list'>

                                    <div className='order-list-left'>
                                        {/* <img src='https://play-lh.googleusercontent.com/et8EtWAtpmeODd9DWvwQ3LXUTo5TThnuFgfENMYiXBDzfHevSheWe1OnrlqOfbxV7r6D'
                                            alt="" /> */}
                                        <Avatar src={item?.avatar?.url} className="avatar" />
                                        <span>{item?.name}</span>
                                    </div>

                                    {/* <div style={{ textAlign: 'center' }}>
                                        <p style={{ color: 'teal', fontFamily: 'GothamMedium', fontSize: '1.4vmax' }}>Price</p>
                                        <p>{item?.id?.$oid}</p>
                                    </div> */}
                                    <div style={{ textAlign: 'center' }}>
                                        <p style={{ color: 'teal', fontFamily: 'GothamMedium', fontSize: '1.4vmax' }}>Similarity</p>
                                        <p>{(item.similarity * 100) + "%".substring(0, 6)}</p>
                                    </div>

                                    <span style={{ color: 'teal', fontFamily: 'GothamMedium', cursor: 'pointer' }}>View</span>
                                </div>
                            ))
                        }
                    </div>
                </div>

            </div>
        </>
    )
}

export default PremiumSearch